import React, { Component } from "react";
import { toast } from "react-toastify";
import _ from "lodash";
import Lbs from "./../common/lbs";
import auth from "../../services/authService";
import { paginate } from "../../js/paginate";
import AttendanceListTable from "./attendanceListTable";
import Pagination from "../common/pagination";
import Select from "../common/select";

class AttendanceList extends Component {
  state = {
    users: [],
    classRooms: [],
    role: [
      { name: "همه", _id: "" },
      { name: "معلم", _id: "3" },
      { name: "دانش آموز", _id: "4" },
    ],
    selectedDay: "1",
    day: "1",
    selectedMonth: "1",
    month: "1",
    selectedYear: "1399",
    year: "1399",
    IsRequestDone: false,
    currentPage: 1,
    pageSize: 10,
    sortColumn: { path: "firstName", order: "asc" },
  };

  async componentDidMount() {
    const { day, month, year } = this.state;
    let res;
    try {
      res = await auth.getAttendances(day, month, year);
    } catch (e) {
      res = false;
      if (e.response.data.error[0] !== "دانش آموزی امروز مدرسه نیومده .") {
        res = "Error";
      }
    }
    try {
      if (res === "Error") throw new Error("Error");
      const propData = this.decodeData(this.props.match.params.data, "&");
      //const sectionId = propData[0];
      const classId = propData[1];
      //console.log("classID:", classId);
      //const className = propData[2];
      const allUsers = await auth.getUsersOfClass(classId);
      let users = this.convertRoleToString(allUsers.data.data);
      if (res) {
        const attendances = res.data.data;
        console.log(attendances);
        users.forEach((u) => {
          attendances.forEach((atndnc) => {
            if (u.user.id === atndnc.user_id) {
              u.attendance = true;
              u.excused = atndnc.excused;
            }
          });
        });
      }
      const date = this.dateForState();
      this.setState({
        users,
        days: date.days,
        months: date.months,
        years: date.years,
        IsRequestDone: true,
      });
    } catch (error) {
      toast.error("مشکلی در ارتباط با سرور پیش آمد :(");
      this.setState({ IsRequestDone: true });
    }
  }
  handleGo = async () => {
    const users = [...this.state.users];
    const {
      selectedDay: day,
      selectedMonth: month,
      selectedYear: year,
    } = this.state;
    this.setState({ IsRequestDone: "" });
    users.forEach((u) => {
      u.attendance = false;
      delete u.excused;
    });
    try {
      const res = await auth.getAttendances(day, month, year);
      const attendances = res.data.data;
      users.forEach((u) => {
        attendances.forEach((atndnc) => {
          if (u.user.id === atndnc.user_id) {
            u.attendance = true;
            u.excused = atndnc.excused;
          }
        });
      });
      this.setState({ IsRequestDone: true, day, month, year, users });
    } catch (error) {
      if (error.response.data.error[0] !== "دانش آموزی امروز مدرسه نیومده .")
        toast.error("مشکلی در ارتباط با سرور پیش آمد :(");
      this.setState({
        IsRequestDone: true,
        day,
        month,
        year,
      });
    }
  };
  handleDelete = async (userId) => {
    const { day, month, year } = this.state;
    const originalUsers = this.state.users;
    let users = JSON.parse(JSON.stringify(this.state.users));
    users.forEach((u) => {
      if (u.user.id === userId) {
        u.attendance = false;
        delete u.excused;
      }
    });
    this.setState({ users });
    try {
      await auth.deleteUserFromAttendance(userId, day, month, year);
      //toast.success(");
    } catch (e) {
      //toast.error("");
      this.setState({ users: originalUsers });
    }
  };
  handleEdit = async (user, excused) => {
    const { day, month, year } = this.state;
    const originalUsers = this.state.users;
    let users = JSON.parse(JSON.stringify(this.state.users));
    users.forEach((u) => {
      if (u.user.id === user.user.id) {
        u.excused = excused;
      }
    });
    this.setState({ users });
    try {
      const data = {
        user_id: user.user.id,
        day,
        month,
        year,
        excused,
      };
      await auth.updateUserAttendaceList(data);
      //toast.success("");
    } catch (e) {
      //toast.error("");
      console.log(e.response);
      this.setState({ users: originalUsers });
    }
  };
  handleAdd = async (user, excused) => {
    const { day, month, year } = this.state;
    const originalUsers = this.state.users;
    let users = JSON.parse(JSON.stringify(this.state.users));
    users.forEach((u) => {
      if (u.user.id === user.user.id) {
        u.attendance = true;
        u.excused = excused;
      }
    });
    this.setState({ users });
    try {
      const data = {
        user_id: user.user.id,
        day,
        month,
        year,
        excused,
      };
      await auth.addUserToAttendaceList(data);
      //toast.success("");
    } catch (e) {
      //   toast.error("");
      this.setState({ users: originalUsers });
    }
  };
  handlePageChange = (page) => {
    this.setState({ currentPage: page });
  };
  handleRoleSelect = ({ currentTarget: input }) => {
    this.setState({ selectedRole: input.value, currentPage: 1 });
  };
  handleDaySelect = ({ currentTarget: input }) => {
    this.setState({ selectedDay: input.value });
  };
  handleMonthSelect = ({ currentTarget: input }) => {
    this.setState({ selectedMonth: input.value });
  };
  handleYearSelect = ({ currentTarget: input }) => {
    this.setState({ selectedYear: input.value });
  };
  handleSort = (sortColumn) => {
    this.setState({ sortColumn });
  };
  getPagedData = () => {
    const {
      pageSize,
      currentPage,
      sortColumn,
      selectedRole,
      users: allUsers,
    } = this.state;

    let filtered = selectedRole
      ? allUsers.filter((u) => {
          return u.user.role._id === selectedRole;
        })
      : allUsers;

    const sorted = _.orderBy(filtered, [sortColumn.path], [sortColumn.order]);

    const users = paginate(sorted, currentPage, pageSize);

    return { totalCount: filtered.length, data: users };
  };
  convertRoleToString = (users) => {
    for (let i = 0; i < users.length; i++) {
      switch (users[i].user.role) {
        case "1":
          users[i].user.role = { name: "مدیر", _id: "1" };
          break;
        case "2":
          users[i].user.role = { name: "معاون", _id: "2" };
          break;
        case "3":
          users[i].user.role = { name: "معلم", _id: "3" };
          break;
        case "4":
          users[i].user.role = { name: "دانش آموز", _id: "4" };
          break;
        default:
          break;
      }
    }
    return users;
  };
  convertId(data) {
    data.forEach((d) => {
      d._id = d.id;
      delete d.id;
    });
    return data;
  }
  mergeClassesAndUsers = (users, usersClasses, classRooms) => {
    users.forEach((u) => {
      u.classRoom = {
        _id: "0",
        name: "بی کلاس",
        section_id: "0",
      };
    });
    usersClasses.forEach((user) => {
      users.forEach((u) => {
        if (u.id === user.user) {
          u.classRoom._id = user.class_id;
          classRooms.forEach((c) => {
            if (c.id === user.class_id) {
              u.classRoom.name = c.name;
            }
          });
        }
      });
    });
    return users;
  };
  getClassRoomObject = (classRoomId) => {
    const { classRooms } = this.state;
    console.log(classRooms);
    let classRoom;
    classRooms.forEach((c) => {
      if (c._id + "" === classRoomId + "") classRoom = c;
    });
    return classRoom;
  };
  dateForState = () => {
    let days = [];
    for (let i = 31; i >= 1; i--) days.unshift({ _id: i, name: i });

    let months = [
      { _id: "1", name: "فروردین" },
      { _id: "2", name: "اردیبهشت" },
      { _id: "3", name: "خرداد" },
      { _id: "4", name: "تیر" },
      { _id: "5", name: "مرداد" },
      { _id: "6", name: "شهریور" },
      { _id: "7", name: "مهر" },
      { _id: "8", name: "آبان" },
      { _id: "9", name: "آذر" },
      { _id: "10", name: "دی" },
      { _id: "11", name: "بهمن" },
      { _id: "12", name: "اسفند" },
    ];
    let years = [
      { _id: "1399", name: "1399" },
      { _id: "1398", name: "1398" },
    ];
    return { days, months, years };
  };
  decodeData(str, splitBy) {
    return str.split(splitBy);
  }
  render() {
    const { length: count } = this.state.users;
    const {
      pageSize,
      currentPage,
      sortColumn,
      IsRequestDone,
      day,
      month,
      year,
    } = this.state;
    if (!IsRequestDone)
      return (
        <div className="card col-12">
          <div className="card-body">
            <Lbs />
          </div>
        </div>
      );
    if (count === 0)
      return (
        <div className="card col-12">
          <div className="card-body">
            <p>چیزی پیدا نشد :(</p>
          </div>
        </div>
      );

    const { totalCount, data: users } = this.getPagedData();

    return (
      <React.Fragment>
        <div className="card col-12">
          <div className="card-body">
            <span className="small text-muted">فیلتر:</span>
            <div className="form-row pr-3">
              {
                <Select
                  name="role"
                  label="نقش کاربر"
                  customClass="col-md-3"
                  options={this.state.role}
                  onChange={this.handleRoleSelect}
                />
              }
            </div>
            <div className="form-row pr-3">
              {
                <Select
                  name="day"
                  label="روز"
                  customClass="col-md-3"
                  options={this.state.days}
                  onChange={this.handleDaySelect}
                />
              }
              {
                <Select
                  name="month"
                  label="ماه"
                  customClass="col-md-3"
                  options={this.state.months}
                  onChange={this.handleMonthSelect}
                />
              }
              {
                <Select
                  name="year"
                  label="سال"
                  customClass="col-md-3"
                  options={this.state.years}
                  onChange={this.handleYearSelect}
                />
              }
              <div className="form-group col-md-3 d-flex align-items-end text-center">
                <button
                  onClick={() => {
                    this.handleGo();
                  }}
                  className="btn btn-outline-primary btn-sm ml-2 p-2 px-3"
                >
                  برو
                </button>
              </div>
            </div>
            <div className="col">
              <p className="small text-muted">
                نتیجه برای {`${year}/${month}/${day}`}
              </p>
              <div className="text-muted my-2">
                <span>
                  <i className="fas fa-user-check text-success" /> حاضر
                </span>
                <span className="mx-3">
                  <i className="fas fa-user-times text-warning" /> غائب (موجه)
                </span>
                <span>
                  <i className="fas fa-user-times text-danger" /> غایب (غیرموجه)
                </span>
              </div>
              <AttendanceListTable
                users={users}
                sortColumn={sortColumn}
                onDelete={this.handleDelete}
                onAdd={this.handleAdd}
                onEdit={this.handleEdit}
                onSort={this.handleSort}
              />
              <Pagination
                itemsCount={totalCount}
                pageSize={pageSize}
                currentPage={currentPage}
                onPageChange={this.handlePageChange}
              />
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default AttendanceList;
