import React from "react";
import { toast } from "react-toastify";
import Joi from "joi-browser";
import Form from "../common/form";
import auth from "../../services/authService";
import { Link, Redirect } from "react-router-dom";
class addWeeklySchedule extends Form {
  state = {
    data: {
      week_day: "0",
      class_id: "0",
      first_bell: "",
      second_bell: "",
      third_bell: "",
      forth_bell: "",
      fifth_bell: "",
    },
    lessons: [],
    weekDays: [
      { name: "شنبه", _id: "0" },
      { name: "یکشنبه", _id: "1" },
      { name: "دوشنبه", _id: "2" },
      { name: "سه شنبه", _id: "3" },
      { name: "چهارشنبه", _id: "4" },
      { name: "پنج شنبه", _id: "5" },
      { name: "جمعه", _id: "6" },
    ],
    validClass: "",
    className: "",
    sectionName: "",
    errors: {},
  };

  schema = {
    week_day: Joi.string()
      .required()
      .label("روز هفته")
      .error((errors) => this.ErrorsLang(errors)),
    class_id: Joi.string()
      .required()
      .label("شماره کلاس")
      .error((errors) => this.ErrorsLang(errors)),
    first_bell: Joi.string()
      .required()
      .label("زنگ اول")
      .error((errors) => this.ErrorsLang(errors)),
    second_bell: Joi.string()
      .required()
      .label("زنگ دوم")
      .error((errors) => this.ErrorsLang(errors)),
    third_bell: Joi.string()
      .required()
      .label("زنگ سوم")
      .error((errors) => this.ErrorsLang(errors)),
    forth_bell: Joi.string()
      .required()
      .label("زنگ چهارم")
      .error((errors) => this.ErrorsLang(errors)),
    fifth_bell: Joi.string()
      .required()
      .label("زنگ پنجم")
      .error((errors) => this.ErrorsLang(errors)),
  };
  async componentDidMount() {
    let { data } = this.state;
    const propData = this.decodeData(this.props.match.params.data, "&");
    const sectionId = propData[0];
    const classId = propData[1];
    const className = propData[2];
    data.week_day = propData[3] ? propData[3] : "0";
    if (classId === "0") return;
    try {
      const allLessons = await auth.getAllLessons();
      const lessons = this.convertSectionToString(allLessons.data.data);
      const filteredLessons = lessons.filter((l) => {
        l._id = l.id;
        l.name = l.title;
        delete l.id;
        delete l.title;
        return l.section._id === sectionId;
      });
      let sectionName = filteredLessons.length
        ? filteredLessons[0].section.name
        : "";

      data.class_id = classId;
      data.first_bell = filteredLessons.length
        ? filteredLessons[0]._id + ""
        : "";
      data.second_bell = filteredLessons.length
        ? filteredLessons[0]._id + ""
        : "";
      data.third_bell = filteredLessons.length
        ? filteredLessons[0]._id + ""
        : "";
      data.forth_bell = filteredLessons.length
        ? filteredLessons[0]._id + ""
        : "";
      data.fifth_bell = filteredLessons.length
        ? filteredLessons[0]._id + ""
        : "";
      this.setState({
        data,
        lessons: filteredLessons,
        validClass: true,
        className,
        sectionName,
      });
    } catch (e) {
      this.setState({ validClass: false });
      toast.error(e.response.data.error[0]);
    }
  }
  convertSectionToString = (lessons) => {
    for (let i = 0; i < lessons.length; i++) {
      switch (lessons[i].section_id) {
        case "0":
          lessons[i].section = { name: "پیش دبستانی", _id: "0" };
          delete lessons[i].section_id;
          break;
        case "1":
          lessons[i].section = { name: "اول", _id: "1" };
          delete lessons[i].section_id;
          break;
        case "2":
          lessons[i].section = { name: "دوم", _id: "2" };
          delete lessons[i].section_id;
          break;
        case "3":
          lessons[i].section = { name: "سوم", _id: "3" };
          delete lessons[i].section_id;
          break;
        case "4":
          lessons[i].section = { name: "چهارم", _id: "4" };
          delete lessons[i].section_id;
          break;
        case "5":
          lessons[i].section = { name: "پنجم", _id: "5" };
          delete lessons[i].section_id;
          break;
        case "6":
          lessons[i].section = { name: "ششم", _id: "6" };
          delete lessons[i].section_id;
          break;
        case "7":
          lessons[i].section = { name: "هفتم", _id: "7" };
          delete lessons[i].section_id;
          break;
        case "8":
          lessons[i].section = { name: "هشتم", _id: "8" };
          delete lessons[i].section_id;
          break;
        case "9":
          lessons[i].section = { name: "نهم", _id: "9" };
          delete lessons[i].section_id;
          break;
        case "10":
          lessons[i].section = { name: "پرسنل", _id: "10" };
          delete lessons[i].section_id;
          break;
        default:
          break;
      }
    }
    return lessons;
  };
  doSubmit = async () => {
    try {
      const { data } = this.state;
      await auth.addSchedule(data);
      // console.log(res);
      toast.success("برنامه با موفقیت ثبت شد.");
      this.setState({ data });
    } catch (ex) {
      toast.error("برنامه ای از قبل وجود دارد");
    }
  };
  decodeData(str, splitBy) {
    return str.split(splitBy);
  }
  render() {
    const propData = this.decodeData(this.props.match.params.data, "&");
    const sectionId = propData[0];
    const classId = propData[1];
    const { className, sectionName, validClass } = this.state;

    return (
      <React.Fragment>
        {validClass === "" && <div id="loader"></div>}
        {validClass === false && <Redirect to="/classes" />}
        {validClass === true && (
          <div className="form-login d-flex align-items-center">
            <div className="card col-lg-7 col-md-5 col-sm-7 col-10 mx-auto mt-5 px-5 pb-5">
              <span className="text-center login-icon mt-n5">
                <i className="far fa-calendar-alt bg-white"></i>
              </span>
              <h4 className="text-center text-muted pt-3">ثبت برنامه هفتگی</h4>
              <span className="text-center text-muted display-6">
                کلاس "{className}" / پایه "{sectionName}"
              </span>
              <form onSubmit={this.handleSubmit}>
                <div className="form-row">
                  {this.renderSelect(
                    "week_day",
                    "روز هفته",
                    this.state.weekDays,
                    "col-lg-6"
                  )}
                </div>
                <div className="form-row">
                  {this.renderSelect(
                    "first_bell",
                    "زنگ اول",
                    this.state.lessons,
                    "col-lg-6"
                  )}
                  {this.renderSelect(
                    "second_bell",
                    "زنگ دوم",
                    this.state.lessons,
                    "col-lg-6"
                  )}
                  {this.renderSelect(
                    "third_bell",
                    "زنگ سوم",
                    this.state.lessons,
                    "col-lg-6"
                  )}
                  {this.renderSelect(
                    "forth_bell",
                    "زنگ چهارم",
                    this.state.lessons,
                    "col-lg-6"
                  )}
                  {this.renderSelect(
                    "fifth_bell",
                    "زنگ پنجم",
                    this.state.lessons,
                    "col-lg-6"
                  )}
                </div>
                <div className="text-center mt-3 pt-4">
                  {this.renderButton("ثبت برنامه", "ml-3")}
                  <Link
                    to={`/weekly-schedule/${sectionId}&${classId}&${className}`}
                    className="btn btn-outline-secondary"
                  >
                    نمایش برنامه
                  </Link>
                </div>
                <span className="d-flex justify-content-center pt-4">
                  <Link to="/classes" className="text-muted">
                    بازگشت به لیست کلاس ها
                  </Link>
                </span>
              </form>
            </div>
          </div>
        )}
      </React.Fragment>
    );
  }
}

export default addWeeklySchedule;
