import React, { Component } from "react";
import Table from "../common/table/table";

class UsersTable extends Component {
  columns = [
    { path: "first_name", label: "نام" },
    { path: "last_name", label: "نام خانوادگی" },
    { path: "nationalCode", label: "کدملی" },
    { path: "section.name", label: "مقطع" },
    { path: "role.name", label: "نقش" },
    { path: "classRoom.name", label: "کلاس" },
    {
      label: "مدیریت کلاس",
      key: "manage",
      content: (
        user // fix it
      ) => (
        <React.Fragment>
          {user.classRoom._id <= 0 && (
            <button
              onClick={() => this.props.onSingleAdd(user)}
              className="btn btn-outline-primary btn-sm ml-2"
              style={{ height: "35px", width: "35px" }}
            >
              <i className="fas fa-plus d-flex align-self-center justify-content-center py-1"></i>
            </button>
          )}

          {user.classRoom._id > 0 && (
            <React.Fragment>
              <button
                onClick={() => this.props.onEdit(user)}
                className="btn btn-outline-primary btn-sm ml-2"
                style={{ height: "35px", width: "35px" }}
              >
                <i className="fas fa-exchange-alt d-flex align-self-center justify-content-center py-1"></i>
              </button>
              <button
                onClick={() => this.props.onClick(user.id)}
                className="btn btn-outline-danger btn-sm"
                style={{ height: "35px", width: "35px" }}
              >
                <i className="fas fa-minus d-flex align-self-center justify-content-center py-1"></i>
              </button>
            </React.Fragment>
          )}
        </React.Fragment>
      ),
    },
    // {
    //   key: "like",
    //   content: (movie) => (
    //     <Like liked={movie.liked} onClick={() => this.props.onLike(movie)} />
    //   ),
    // },
  ];
  render() {
    const { users, onSort, sortColumn } = this.props;

    return (
      <Table
        columns={this.columns}
        data={users}
        sortColumn={sortColumn}
        onSort={onSort}
      />
    );
  }
}

export default UsersTable;
