import React, { Component } from "react";
import { toast } from "react-toastify";
import _ from "lodash";
import $ from "jquery";
import Modal from "../common/modal";
import Lbs from "./../common/lbs";
import auth from "../../services/authService";
import { paginate } from "../../js/paginate";
import ScheduleTable from "./scheduleTable";
import Pagination from "../common/pagination";
import { Link } from "react-router-dom";

class ViewWeeklySchedule extends Component {
  state = {
    schedule: [], //id,week_day,class_id,first...fifth_bell
    modalData: "",
    IsRequestDone: false,
    currentPage: 1,
    pageSize: 7,
    sortColumn: { path: "week_day._id", order: "asc" },
  };

  async componentDidMount() {
    const propData = this.decodeData(this.props.match.params.data, "&");
    const sectionId = propData[0];
    const classId = propData[1];
    try {
      const res = await auth.getAllSchedule(classId);
      const res2 = await auth.getLessonsBySecton(sectionId);
      this.lessonConvertSectionToString(res.data.data, res.data.data);
      const schedule = this.fillEmpety(
        this.convertSectionToString(
          this.lessonConvertSectionToString(res2.data.data, res.data.data)
        )
      );
      console.log(res.data.data);
      this.setState({ schedule, IsRequestDone: true });
    } catch (error) {
      toast.error("مشکلی در ارتباط با سرور پیش آمد :(");
      this.setState({ IsRequestDone: true });
    }
  }

  handleDelete = async ({ classId, week_day }) => {
    const originalClasses = this.state.schedule;
    const schedule = this.state.schedule.filter(
      (s) => s.week_day._id !== week_day
    );
    this.setState({ schedule: this.fillEmpety(schedule) });
    if (classId > 0) {
      try {
        await auth.deleteSchedule(classId, week_day);
        toast.success("کلاس با موفقیت حذف شد.");
      } catch (e) {
        toast.error("حذف کلاس با شکست مواجه شد.");
        this.setState({ schedule: originalClasses });
      }
    } else {
      toast.error("برنامه ای برا حذف وجود ندارد.");
    }
  };
  handleModal = (modalData) => {
    this.setState({ modalData });
    $("#delete_schedule").modal("show");
  };
  handleEdit = (schedule) => {
    const propData = this.decodeData(this.props.match.params.data, "&");
    const sectionId = propData[0];
    const classId = propData[1];
    const className = propData[2];
    window.location =
      process.env.PUBLIC_URL +
      `/edit-weekly-schedule/${sectionId}&${classId}&${className}&${schedule.week_day._id}`;
  };
  handleAdd = (schedule) => {
    const propData = this.decodeData(this.props.match.params.data, "&");
    const sectionId = propData[0];
    const classId = propData[1];
    const className = propData[2];
    window.location =
      process.env.PUBLIC_URL +
      `/add-weekly-schedule/${sectionId}&${classId}&${className}&${schedule.week_day._id}`;
  };
  handlePageChange = (page) => {
    this.setState({ currentPage: page });
  };

  handleSort = (sortColumn) => {
    //this.setState({ sortColumn });
  };
  decodeData(str, splitBy) {
    return str.split(splitBy);
  }
  getPagedData = () => {
    const {
      pageSize,
      currentPage,
      sortColumn,
      schedule: allSchedule,
    } = this.state;

    const sorted = _.orderBy(
      allSchedule,
      [sortColumn.path],
      [sortColumn.order]
    );

    const schedule = paginate(sorted, currentPage, pageSize);

    return { totalCount: allSchedule.length, data: schedule };
  };
  convertSectionToString = (schedule) => {
    for (let i = 0; i < schedule.length; i++) {
      switch (schedule[i].week_day) {
        case "0":
          schedule[i].week_day = { name: "شنبه", _id: "0" };
          break;
        case "1":
          schedule[i].week_day = { name: "یکشنبه", _id: "1" };
          break;
        case "2":
          schedule[i].week_day = { name: "دوشنبه", _id: "2" };
          break;
        case "3":
          schedule[i].week_day = { name: "سه شنبه", _id: "3" };
          break;
        case "4":
          schedule[i].week_day = { name: "چهارشنبه", _id: "4" };
          break;
        case "5":
          schedule[i].week_day = { name: "پنجشنبه", _id: "5" };
          break;
        case "6":
          schedule[i].week_day = { name: "جمعه", _id: "6" };
          break;
        default:
          break;
      }
    }
    return schedule;
  };
  lessonConvertSectionToString = (allLessons, schedule) => {
    allLessons.forEach((lesson) => {
      schedule.forEach((s) => {
        if (s.first_bell === lesson.id) s.first_bell = lesson;
        if (s.second_bell === lesson.id) s.second_bell = lesson;
        if (s.third_bell === lesson.id) s.third_bell = lesson;
        if (s.forth_bell === lesson.id) s.forth_bell = lesson;
        if (s.fifth_bell === lesson.id) s.fifth_bell = lesson;
      });
    });
    return schedule;
  };
  fillEmpety = (schedule) => {
    let tempSchedule = [
      {
        id: "-1",
        week_day: { _id: "0", name: "شنبه" },
        class_id: "0",
      },
      {
        id: "-2",
        week_day: { _id: "1", name: "یکشنبه" },
        class_id: "0",
      },
      {
        id: "-3",
        week_day: { _id: "2", name: "دوشنبه" },
        class_id: "0",
      },
      {
        id: "-4",
        week_day: { _id: "3", name: "سه شنبه" },
        class_id: "0",
      },
      {
        id: "-5",
        week_day: { _id: "4", name: "چهارشنبه" },
        class_id: "0",
      },
      {
        id: "-6",
        week_day: { _id: "5", name: "پنجشنبه" },
        class_id: "0",
      },
      {
        id: "-7",
        week_day: { _id: "6", name: "جمعه" },
        class_id: "0",
      },
    ];
    schedule.forEach((s) => {
      tempSchedule = tempSchedule.filter((ts) => {
        return s.week_day._id !== ts.week_day._id;
      });
    });
    return schedule.concat(tempSchedule);
  };
  render() {
    const { length: count } = this.state.schedule;
    const { pageSize, currentPage, sortColumn, IsRequestDone } = this.state;
    if (!IsRequestDone)
      return (
        <div className="card col-12">
          <div className="card-body">
            <Lbs />
          </div>
        </div>
      );
    if (count === 0)
      return (
        <div className="card col-12">
          <div className="card-body">
            <p>چیزی پیدا نشد :(</p>
          </div>
        </div>
      );

    const { totalCount, data: schedule } = this.getPagedData();

    return (
      <React.Fragment>
        <Modal
          modalId="delete_schedule"
          dataId={this.state.modalData}
          handleDelete={this.handleDelete}
        />
        <div className="card col-12">
          <div className="card-body">
            <div className="col">
              <ScheduleTable
                schedule={schedule}
                sortColumn={sortColumn}
                onClick={this.handleModal}
                onEdit={this.handleEdit}
                onAdd={this.handleAdd}
                onSort={this.handleSort}
              />
              <Pagination
                itemsCount={totalCount}
                pageSize={pageSize}
                currentPage={currentPage}
                onPageChange={this.handlePageChange}
              />
              <span className="d-flex justify-content-center pt-4">
                <Link to="/classes" className="text-muted">
                  بازگشت به لیست کلاس ها
                </Link>
              </span>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default ViewWeeklySchedule;
