import React, { Component } from "react";
import Table from "../common/table/table";

class ScheduleTable extends Component {
  columns = [
    //{ path: "week_day._id", label: "آیدی هفته" },
    { path: "week_day.name", label: "روز هفته" },
    { path: "first_bell.title", label: "زنگ اول" },
    { path: "second_bell.title", label: "زنگ دوم" },
    { path: "third_bell.title", label: "زنگ سوم" },
    { path: "forth_bell.title", label: "زنگ چهارم" },
    { path: "fifth_bell.title", label: "زنگ پنجم" },
    {
      label: "مدیریت",
      key: "manage",
      content: (
        schedule // fix it
      ) =>
        schedule.class_id > 0 ? (
          <React.Fragment>
            <button
              onClick={() => this.props.onEdit(schedule)}
              className="btn btn-outline-info btn-sm mx-2"
              style={{ height: "35px", width: "35px" }}
            >
              <i className="fas fa-edit d-flex align-self-center justify-content-center py-1"></i>
            </button>
            <button
              onClick={() =>
                this.props.onClick({
                  classId: schedule.class_id,
                  week_day: schedule.week_day._id,
                })
              }
              className="btn btn-outline-danger btn-sm"
              style={{ height: "35px", width: "35px" }}
            >
              <i className="fas fa-trash-alt d-flex align-self-center justify-content-center py-1"></i>
            </button>
          </React.Fragment>
        ) : (
          <button
            onClick={() => this.props.onAdd(schedule)}
            className="btn btn-outline-info btn-sm"
            style={{ height: "35px", width: "35px" }}
          >
            <i className="far fa-calendar-plus d-flex align-self-center justify-content-center py-1"></i>
          </button>
        ),
    },
  ];
  render() {
    const { schedule, onSort, sortColumn } = this.props;

    return (
      <Table
        columns={this.columns}
        data={schedule}
        sortColumn={sortColumn}
        onSort={onSort}
      />
    );
  }
}

export default ScheduleTable;
