import React, { Component } from "react";
import Table from "../common/table/table";

class AttendanceListTable extends Component {
  columns = [
    { path: "user.first_name", label: "نام" },
    { path: "user.last_name", label: "نام خانوادگی" },
    { path: "user.role.name", label: "نقش" },
    {
      label: "وضعیت",
      key: "attendance",
      content: (user) => (
        <React.Fragment>
          {user.attendance && user.excused === "0" && (
            <i className="fas fa-user-check text-success d-flex items-align-center justify-content-center mt-2 py-1" />
          )}
          {user.attendance && user.excused === "1" && (
            <i className="fas fa-user-times text-warning d-flex items-align-center justify-content-center mt-2 py-1" />
          )}
          {!user.attendance && (
            <i className="fas fa-user-times text-danger d-flex items-align-center justify-content-center mt-2 py-1" />
          )}
        </React.Fragment>
      ),
    },
    {
      label: "مدیریت وضعیت",
      key: "manage",
      content: (user) => (
        <React.Fragment>
          {!user.attendance && (
            <React.Fragment>
              <button
                onClick={() => this.props.onAdd(user, "0")}
                className="btn btn-outline-success btn-sm ml-2"
                style={{ height: "35px", width: "35px" }}
              >
                <i className="fas fa-user-check d-flex align-self-center justify-content-center py-1 a"></i>
              </button>
              <button
                onClick={() => this.props.onAdd(user, "1")}
                className="btn btn-outline-warning btn-sm ml-2"
                style={{ height: "35px", width: "35px" }}
              >
                <i className="fas fa-user-times d-flex align-self-center justify-content-center py-1 a"></i>
              </button>
            </React.Fragment>
          )}

          {user.attendance && user.excused === "1" && (
            <button
              onClick={() => this.props.onEdit(user, "0")}
              className="btn btn-outline-success btn-sm ml-2"
              style={{ height: "35px", width: "35px" }}
            >
              <i className="fas fa-user-check d-flex align-self-center justify-content-center py-1 b"></i>
            </button>
          )}

          {user.attendance && (
            <button
              onClick={() => this.props.onDelete(user.user.id)}
              className="btn btn-outline-danger btn-sm ml-2"
              style={{ height: "35px", width: "35px" }}
            >
              <i className="fas fa-user-times d-flex align-self-center justify-content-center py-1 b"></i>
            </button>
          )}
          {user.attendance && user.excused === "0" && (
            <button
              onClick={() => this.props.onEdit(user, "1")}
              className="btn btn-outline-warning btn-sm ml-2"
              style={{ height: "35px", width: "35px" }}
            >
              <i className="fas fa-user-times d-flex align-self-center justify-content-center py-1 b"></i>
            </button>
          )}
        </React.Fragment>
      ),
    },
    // {
    //   key: "like",
    //   content: (movie) => (
    //     <Like liked={movie.liked} onClick={() => this.props.onLike(movie)} />
    //   ),
    // },
  ];
  render() {
    const { users, onSort, sortColumn } = this.props;

    return (
      <Table
        columns={this.columns}
        data={users}
        sortColumn={sortColumn}
        onSort={onSort}
      />
    );
  }
}

export default AttendanceListTable;
