import React, { Component } from "react";
import { toast } from "react-toastify";
import _ from "lodash";
import $ from "jquery";
import Modal from "../common/modal";
import Lbs from "./../common/lbs";
import auth from "../../services/authService";
import { paginate } from "../../js/paginate";
import AddUserToClassTable from "./addUserToClassTable";
import Pagination from "../common/pagination";
import Select from "../common/select";

class AddUserToClass extends Component {
  state = {
    users: [],
    classRooms: [],
    role: [
      { name: "همه", _id: "" },
      { name: "مدیر", _id: "1" },
      { name: "معاون", _id: "2" },
      { name: "معلم", _id: "3" },
      { name: "دانش آموز", _id: "4" },
    ],
    section: [
      { name: "همه", _id: "" },
      { name: "پیش دبستانی", _id: "0" },
      { name: "اول", _id: "1" },
      { name: "دوم", _id: "2" },
      { name: "سوم", _id: "3" },
      { name: "چهارم", _id: "4" },
      { name: "پنجم", _id: "5" },
      { name: "ششم", _id: "6" },
      { name: "هفتم", _id: "7" },
      { name: "هشتم", _id: "8" },
      { name: "نهم", _id: "9" },
      { name: "پرسنل", _id: "10" },
    ],
    modalData: "",
    IsRequestDone: false,
    currentPage: 1,
    pageSize: 10,
    sortColumn: { path: "firstName", order: "asc" },
  };

  async componentDidMount() {
    //const kelas = [{ _id: "", name: "همه" }, ...getkelas()];
    //this.setState({ users, kelas });
    try {
      const allUsers = await auth.getAllUsers();
      let allClassRooms = await auth.getAllClasses();
      const UsersClasses = await auth.getUsersClasses();
      const users = this.mergeClassesAndUsers(
        this.filterUsers(
          this.convertRoleToString(
            this.convertSectionToString(allUsers.data.data)
          )
        ),
        UsersClasses.data.data,
        allClassRooms.data.data
      );
      this.convertId(allClassRooms.data.data);
      let classRoomsSort = [...allClassRooms.data.data];
      classRoomsSort.unshift({ _id: "", name: "همه", section_id: "0" });
      this.setState({
        users,
        classRoomsSort,
        IsRequestDone: true,
        classRooms: allClassRooms.data.data,
        selectedClassRoom: allClassRooms.data.data[0]._id,
      });
    } catch (error) {
      toast.error("مشکلی در ارتباط با سرور پیش آمد :(");
      this.setState({ IsRequestDone: true });
    }
  }
  handleDelete = async (userId) => {
    const originalUsers = this.state.users;
    let users = JSON.parse(JSON.stringify(this.state.users));
    users.forEach((u) => {
      if (u.id === userId)
        u.classRoom = { _id: "0", name: "بی کلاس", section_id: "0" };
    });
    this.setState({ users });
    try {
      await auth.deleteUserFromClass(userId);
      toast.success("کاربر با موفقیت از کلاس حذف شد.");
    } catch (e) {
      toast.error("حذف کاربر از کلاس با شکست مواجه شد.");
      this.setState({ users: originalUsers });
    }
  };
  handleModal = (modalData) => {
    this.setState({ modalData });
    $("#delete_user").modal("show");
  };
  handleModal2 = (modalData) => {
    this.setState({ modalData });
    $("#delete_all_users_from_class").modal("show");
  };
  handleRemoveAllUser = async (class_id) => {
    const originalUsers = this.state.users;
    let users = JSON.parse(JSON.stringify(this.state.users));
    users.forEach((u) => {
      console.log(u.classRoom);
      if (u.classRoom._id + "" === class_id + "")
        u.classRoom = { _id: "0", name: "بی کلاس", section_id: "0" };
    });
    this.setState({ users });
    try {
      await auth.deleteAllUsesrFromClass(class_id);
      toast.success("کاربران با موفقیت از کلاس حذف شد.");
    } catch (e) {
      toast.error(e.response.data.message);
      this.setState({ users: originalUsers });
    }
  };
  handleEdit = async (user) => {
    const originalUsers = this.state.users;
    let users = JSON.parse(JSON.stringify(this.state.users));
    users.forEach((u) => {
      if (u.id === user.id) {
        u.classRoom = this.getClassRoomObject(this.state.selectedClassRoom);
      }
    });
    this.setState({ users });
    try {
      const data = {
        user: user.id,
        class_id: this.state.selectedClassRoom,
      };
      await auth.updateUserClass(data);
      toast.success("کلاس کاربر با موفقیت تغییر پیدا کرد.");
    } catch (e) {
      toast.error("عملیات تغییر کلاس کاربر شکست مواجه شد.");
      this.setState({ users: originalUsers });
    }
  };
  handleSingleAdd = async (user) => {
    const originalUsers = this.state.users;
    let users = JSON.parse(JSON.stringify(this.state.users));
    users.forEach((u) => {
      if (u.id === user.id) {
        u.classRoom = this.getClassRoomObject(this.state.selectedClassRoom);
      }
    });
    this.setState({ users });
    try {
      const data = {
        user: user.id,
        class_id: this.state.selectedClassRoom,
      };
      await auth.addUserToClass(data);
      toast.success("کاربر با موفقیت به کلاس اضافه شد.");
    } catch (e) {
      toast.error("عملیات اضافه کردن کاربر به کلاس با شکست مواجه شد.");
      this.setState({ users: originalUsers });
    }
  };

  handlePageChange = (page) => {
    this.setState({ currentPage: page });
  };
  handleSectionSelect = ({ currentTarget: input }) => {
    this.setState({ selectedSection: input.value, currentPage: 1 });
  };
  handleRoleSelect = ({ currentTarget: input }) => {
    this.setState({ selectedRole: input.value, currentPage: 1 });
  };
  handleClassRoomSortSelect = ({ currentTarget: input }) => {
    this.setState({ selectedClassRoomSort: input.value, currentPage: 1 });
  };
  handleClassRoomSelect = ({ currentTarget: input }) => {
    this.setState({ selectedClassRoom: input.value });
  };
  handleSort = (sortColumn) => {
    this.setState({ sortColumn });
  };

  getPagedData = () => {
    const {
      pageSize,
      currentPage,
      sortColumn,
      //   selectedKelas,
      selectedClassRoomSort,
      selectedRole,
      selectedSection,
      users: allUsers,
    } = this.state;

    // const filtered =
    //   selectedKelas && selectedKelas._id
    //     ? allUsers.filter((u) => {
    //         return u.kelas === selectedKelas._id;
    //       })
    //     : allUsers;
    let filtered = selectedRole
      ? allUsers.filter((u) => {
          return u.role._id === selectedRole;
        })
      : allUsers;
    filtered = selectedSection
      ? filtered.filter((u) => {
          return u.section._id === selectedSection;
        })
      : filtered;
    filtered = selectedClassRoomSort
      ? filtered.filter((u) => {
          return u.classRoom._id + "" === selectedClassRoomSort + "";
        })
      : filtered;

    const sorted = _.orderBy(filtered, [sortColumn.path], [sortColumn.order]);

    const users = paginate(sorted, currentPage, pageSize);

    return { totalCount: filtered.length, data: users };
  };
  convertSectionToString = (users) => {
    for (let i = 0; i < users.length; i++) {
      switch (users[i].section) {
        case "0":
          users[i].section = { name: "پیش دبستانی", _id: "0" };
          break;
        case "1":
          users[i].section = { name: "اول", _id: "1" };
          break;
        case "2":
          users[i].section = { name: "دوم", _id: "2" };
          break;
        case "3":
          users[i].section = { name: "سوم", _id: "3" };
          break;
        case "4":
          users[i].section = { name: "چهارم", _id: "4" };
          break;
        case "5":
          users[i].section = { name: "پنجم", _id: "5" };
          break;
        case "6":
          users[i].section = { name: "ششم", _id: "6" };
          break;
        case "7":
          users[i].section = { name: "هفتم", _id: "7" };
          break;
        case "8":
          users[i].section = { name: "هشتم", _id: "8" };
          break;
        case "9":
          users[i].section = { name: "نهم", _id: "9" };
          break;
        case "10":
          users[i].section = { name: "پرسنل", _id: "10" };
          break;
        default:
          break;
      }
    }
    return users;
  };
  convertRoleToString = (users) => {
    for (let i = 0; i < users.length; i++) {
      switch (users[i].role) {
        case "1":
          users[i].role = { name: "مدیر", _id: "1" };
          break;
        case "2":
          users[i].role = { name: "معاون", _id: "2" };
          break;
        case "3":
          users[i].role = { name: "معلم", _id: "3" };
          break;
        case "4":
          users[i].role = { name: "دانش آموز", _id: "4" };
          break;
        default:
          break;
      }
    }
    return users;
  };
  convertId(data) {
    data.forEach((d) => {
      d._id = d.id;
      delete d.id;
    });
    return data;
  }
  filterUsers = (users) => {
    //only student and teachers!
    users = users.filter((user) => {
      return user.role._id >= 3;
    });
    return users;
  };
  mergeClassesAndUsers = (users, usersClasses, classRooms) => {
    users.forEach((u) => {
      u.classRoom = {
        _id: "0",
        name: "بی کلاس",
        section_id: "0",
      };
    });
    usersClasses.forEach((user) => {
      users.forEach((u) => {
        if (u.id === user.user) {
          u.classRoom._id = user.class_id;
          classRooms.forEach((c) => {
            if (c.id === user.class_id) {
              u.classRoom.name = c.name;
            }
          });
        }
      });
    });
    return users;
  };
  getClassRoomObject = (classRoomId) => {
    const { classRooms } = this.state;
    console.log(classRooms);
    let classRoom;
    classRooms.forEach((c) => {
      if (c._id + "" === classRoomId + "") classRoom = c;
    });
    return classRoom;
  };
  render() {
    const { length: count } = this.state.users;
    const { pageSize, currentPage, sortColumn, IsRequestDone } = this.state;
    if (!IsRequestDone)
      return (
        <div className="card col-12">
          <div className="card-body">
            <Lbs />
          </div>
        </div>
      );
    if (count === 0)
      return (
        <div className="card col-12">
          <div className="card-body">
            <p>چیزی پیدا نشد :(</p>
          </div>
        </div>
      );

    const { totalCount, data: users } = this.getPagedData();

    return (
      <React.Fragment>
        <Modal
          modalId="delete_user"
          dataId={this.state.modalData}
          handleDelete={this.handleDelete}
        />
        <Modal
          modalId="delete_all_users_from_class"
          dataId={this.state.modalData}
          handleDelete={this.handleRemoveAllUser}
        />
        <div className="card col-12">
          <div className="card-body">
            {/* <div className="col-3">
              <ListGroup
                items={this.state.kelas}
                selectedItem={this.state.selectedKelas}
                onItemSelect={this.handleKelasSelect}
              />
            </div> */}
            <span className="small text-muted">فیلتر:</span>
            <div className="form-row pr-3">
              {
                <Select
                  name="role"
                  label="نقش کاربر"
                  customClass="col-md-3"
                  options={this.state.role}
                  onChange={this.handleRoleSelect}
                />
              }
              {
                <Select
                  name="section"
                  label="مقطع"
                  customClass="col-md-3"
                  options={this.state.section}
                  onChange={this.handleSectionSelect}
                />
              }
              {
                <Select
                  name="classRoomSort"
                  label="کلاس"
                  customClass="col-md-3"
                  options={this.state.classRoomsSort}
                  onChange={this.handleClassRoomSortSelect}
                />
              }
            </div>

            <div className="form-row pr-3">
              {
                <Select
                  name="classRoom"
                  label="افزودن کاربر(ها) به کلاس:"
                  customClass="col-md-3"
                  options={this.state.classRooms}
                  onChange={this.handleClassRoomSelect}
                />
              }
              <div className="form-group col-md-5 d-flex align-items-end">
                <button
                  onClick={() =>
                    this.handleModal2(this.state.selectedClassRoom)
                  }
                  className="btn btn-outline-danger btn-sm p-2"
                >
                  حذف تمامی کاربران از کلاس
                </button>
              </div>
            </div>
            <div className="col">
              <p className="small text-muted">نتیجه: {totalCount} کاربر</p>
              <AddUserToClassTable
                users={users}
                sortColumn={sortColumn}
                //onLike={this.handleLike}
                onClick={this.handleModal}
                onEdit={this.handleEdit}
                onSingleAdd={this.handleSingleAdd}
                onSort={this.handleSort}
              />
              <Pagination
                itemsCount={totalCount}
                pageSize={pageSize}
                currentPage={currentPage}
                onPageChange={this.handlePageChange}
              />
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default AddUserToClass;
